import { useCallback, useEffect, useMemo } from 'react'
import { useFieldArray, useForm } from 'react-hook-form'
import { SelectCustomOptionType } from 'components/atoms/SelectCustomized/SelectCustomized.spec'
import { CheckboxBatteryItemType } from 'components/molecules/CheckboxBattery/CheckboxBattery.spec'

export const Names = {
  DATE: `DATE`,
  OCCUPATION: `OCCUPATION`,
  WINTER: `WINTER`,
  GENDER: `GENDER`,
  REASON: `REASON`,
  COLORS: `COLORS`,
  MAP: `MAP`
}

export const FieldsData = {
  [Names.DATE]: {
    name: Names.DATE,
    label: `Дата заполнения`,
    errorMessage: `Пожалуйста, выберите дату`
  },
  [Names.OCCUPATION]: {
    name: Names.OCCUPATION,
    label: `Ваш род занятий`,
    errorMessage: `Пожалуйста, укажите Ваш род занятий`
  },
  [Names.OCCUPATION]: {
    name: Names.OCCUPATION,
    label: `Ваш род занятий`,
    errorMessage: `Пожалуйста, укажите Ваш род занятий`
  },
  [Names.WINTER]: {
    name: Names.WINTER,
    label: `Сколько времени Вы проводите на улице зимой?`,
    errorMessage: `Пожалуйста, поделитесь хотя бы ориентировочной оценкой`
  },
  [Names.GENDER]: {
    name: Names.GENDER,
    label: `Ваш пол`,
    errorMessage: `Пожалуйста, укажите Ваш пол`,
    options: [
      {
        label: `Мужской`,
        value: `male`
      },
      {
        label: `Женский`,
        value: `female`
      },
    ] as SelectCustomOptionType[]
  },
  [Names.REASON]: {
    name: Names.REASON,
    label: `Почему вы выбрали эти цвета?`,
    errorMessage: `Пожалуйста, хотя бы кратко поделитесь причинами выбора`
  },
  [Names.COLORS]: {
    name: Names.COLORS,
    label: `Какие цвета Вы считаете наиболее подходящими для освещения улиц Москвы? Можно выбрать несколько ответов`,
    errorMessage: `Пожалуйста, выберите хотя бы один цвет`,
    options: [
      `Желтый`,
      `Оранжевый`,
      `Оттенки белого`,
      `Зеленый`,
      `Красный`,
      `Синий`,
      `Фиолетовый`,
    ],
    labelOther: `Другой вариант`
  },
  [Names.MAP]: {
    name: Names.MAP,
    label: `Поставьте, пожалуйста, метку на карте`,
    errorMessage: `Пожалуйста, выберите метку`
  }
}

function useManageForm () {
  const colorsDefaultValues: CheckboxBatteryItemType[] = useMemo(() => {
    const baseResults = (FieldsData[Names.COLORS].options as string[]).map((item, index) => ({
      id: index,
      label: item,
      value: false
    }))

    return [
      ...baseResults,
      {
        id: baseResults.length,
        label: ``,
        value: false,
        isCustom: true
      }
    ]
  }, [])

  const {control, handleSubmit, formState} = useForm({
    mode: `onChange`,
    defaultValues: {
      [Names.DATE]: Date.now(),
      [Names.OCCUPATION]: ``,
      [Names.WINTER]: ``,
      [Names.GENDER]: null,
      [Names.REASON]: ``,
      [Names.COLORS]: colorsDefaultValues,
      [Names.MAP]: null
    },
  })

  const {fields} = useFieldArray({
    name: Names.COLORS,
    control,
    rules: {
      validate: value => {
        console.log(value)
        const isAnyChecked = (value as CheckboxBatteryItemType[]).reduce((total, item) => {
          if (!total && item.value) {
            total = true
          }

          return total
        }, false)

        return isAnyChecked || FieldsData[Names.COLORS].errorMessage
      }
    }
  })

  const handleSubmitData = useCallback(data => {
    alert(`Введены следующие значения:` + JSON.stringify(data))
  }, [])

  return {
    control,
    handleSubmitData: handleSubmit(handleSubmitData),
    colorOptions: fields,
    colorErrorMessage: formState.errors[Names.COLORS]?.root?.message
  }
}

export default useManageForm
